audio {
    display: none;
}

.circleContainer {
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.circle {
    background: #fafcf7;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    overflow: hidden;
    -webkit-animation:grow 10s infinite;
}

@-webkit-keyframes grow {
    0% {
        -webkit-transform: scale( 0.01 );-ms-transform: scale( 0.01 );transform: scale( 0.01 );
    }
    
    50% {
        -webkit-transform: scale( 1 );-ms-transform: scale( 1 );transform: scale( 1 );
    }
    
    100% {
        -webkit-transform: scale( 0.01 );-ms-transform: scale( 0.01 );transform: scale( 0.01 );
    }
}



